import React from "react";
import {
    Container,
    makeStyles,
    ThemeProvider,
    createMuiTheme,
} from "@material-ui/core";
import SetupPage from "./SetupPage";
import WidgetPage from "./WidgetPage";
import { useSpring, animated } from "react-spring";
import clsx from "clsx";
import useLocalStorage from "./useLocalStorage";

// https://material-ui.com/customization/default-theme/
// https://colorhunt.co/palette/167893
const theme = createMuiTheme({
    palette: {
        primary: {
            main: "#3282b8",
            light: "#bbe1fa",
        },
        secondary: {
            main: "#0f4c75",
            light: "#b7bdc3",
        },
        text: {
            primary: "#fcf8ec",
            secondary: "#d0e8f2",
        },
        background: {
            default: "#1b262c",
            paper: "#0f4c75",
        },
    },
});

const useStyle = makeStyles((theme) => ({
    card: {
        willChange: "transform, opacity",
        position: "absolute",
        margin: 0,
        left: 0,
        right: 0,
    },
    front: {
        top: "10%",
    },
    back: {},
    top: {
        zIndex: 99,
    },
}));

function App() {
    const s = useStyle();
    const [savedRecs, saveRecs] = useLocalStorage<{
        from: string;
        to: string;
    } | null>("@recipients", null);
    const [isFront, setIsFront] = React.useState(savedRecs ? false : true);
    const [recipients, setRecipients] = React.useState(
        savedRecs || { from: "", to: "" }
    );

    const { transform, opacity } = useSpring({
        opacity: isFront ? 0 : 1,
        transform: `perspective(1000px) rotateX(${isFront ? 0 : 180}deg)`,
        config: { mass: 6, tension: 400, friction: 80 },
    });

    return (
        <ThemeProvider theme={theme}>
            <Container maxWidth="lg">
                <div
                    style={{
                        position: "relative",
                    }}
                >
                    <animated.div
                        className={clsx(s.card, s.front, {
                            [s.top]: isFront,
                        })}
                        style={{
                            opacity: opacity.interpolate(
                                (o) => 1 - (o as number)
                            ),
                            transform,
                        }}
                    >
                        <SetupPage
                            initValue={recipients}
                            onDone={(from, to) => {
                                saveRecs({ from, to });
                                setRecipients({ from, to });

                                setIsFront(false);
                            }}
                        />
                    </animated.div>
                    <animated.div
                        className={clsx(s.card, s.back, {
                            [s.top]: !isFront,
                        })}
                        style={{
                            opacity,
                            transform: transform.interpolate(
                                (t) => `${t} rotateX(180deg)`
                            ),
                        }}
                    >
                        <WidgetPage
                            recipients={recipients}
                            onExit={() => {
                                saveRecs(null);
                                setIsFront(true);
                            }}
                        />
                    </animated.div>
                </div>
            </Container>
        </ThemeProvider>
    );
}

export default App;
