import React from "react";
import LoveButton from "./LoveButton";
import {
    Box,
    Table,
    TableContainer,
    TableRow,
    TableCell,
    TableHead,
    TableBody,
    Paper,
    makeStyles,
    IconButton,
    Typography,
    Grid,
} from "@material-ui/core";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import { zoomIn } from "react-animations";
import { addLove, getLovesFor } from "./dbUtils";
import { capFChar, groupByHour, serverizeStr } from "./utils";
import clsx from "clsx";
import { Line } from "react-chartjs-2";

const useStyle = makeStyles((theme) => ({
    center: {
        textAlign: "center",
    },
    headCell: {
        fontSize: "1.2rem",
        fontWeight: "bold",
    },
    grid: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    "@keyframes zoomIn": zoomIn,
    zoomIn: {
        animation: `$zoomIn 500ms`,
    },
    bgPrimary: {
        backgroundColor: theme.palette.primary.main,
    },
    bgSecondary: {
        backgroundColor: theme.palette.secondary.main,
    },
}));

export default function WidgetPage({ onExit, recipients }) {
    const s = useStyle();
    const [loves, setLoves] = React.useState<undefined | any[]>();

    React.useEffect(() => {
        setLoves(undefined);
        if (
            !recipients.from ||
            !recipients.to ||
            recipients.from === "" ||
            recipients.to === ""
        ) {
            return;
        }
        function fetchLoves() {
            getLovesFor(recipients.from, function (res) {
                setLoves(res);
            });
        }

        if (recipients.from !== "" && recipients.to !== "") {
            fetchLoves();
        }
    }, [recipients]);

    const byHour = groupByHour(loves || []);
    const chartData = {
        labels: [...Array(24).keys()].map(n => `${n}:00`),
        datasets: [{
            label: recipients.from,
            borderColor: "#F00",
            backgroundColor: "#F00",
            fill: false,
            data: byHour[serverizeStr(recipients.from)]
        }, {
            label: recipients.to,
            borderColor: "#00F",
            backgroundColor: "#00F",
            fill: false,
            data: byHour[serverizeStr(recipients.to)]
        }]
    }

    return (
        <Box className={s.center}>
            <IconButton
                style={{
                    position: "absolute",
                    zIndex: 9999,
                    left: 0,
                    top: 0,
                }}
                onClick={onExit}
            >
                <ExitToAppIcon
                    style={{
                        transform: "scaleX(-1)",
                        fontSize: "2.5rem",
                    }}
                    color="primary"
                />
            </IconButton>

            <Grid container spacing={3} className={s.grid}>
                <Grid item xs={12} md={4}>
                    <Typography color="textPrimary" variant="h4">
                        {capFChar(recipients.from)}
                    </Typography>
                </Grid>
                <Grid item xs={12} md={4}>
                    <LoveButton
                        onClick={async () => {
                            await addLove(recipients.from, recipients.to);
                        }}
                    />
                </Grid>
                <Grid item xs={12} md={4}>
                    <Typography color="textPrimary" variant="h4">
                        {capFChar(recipients.to)}
                    </Typography>
                </Grid>

                {loves && loves.length !== 0 ? (
                    <Grid item xs={12}>
                        <TableContainer
                            component={Paper}
                            className={s.zoomIn}
                            style={{ maxHeight: "40vh", overflow: "auto" }}
                        >
                            <Table stickyHeader>
                                <TableHead>
                                    <TableRow>
                                        <TableCell className={s.headCell}>
                                            From
                                </TableCell>
                                        <TableCell className={s.headCell} align="right">
                                            To
                                </TableCell>
                                        <TableCell className={s.headCell} align="right">
                                            Time
                                </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {loves.map((l, i) => (
                                        <TableRow
                                            className={clsx(s.bgPrimary, {
                                                [s.bgSecondary]:
                                                    capFChar(l.from) ===
                                                    capFChar(recipients.from),
                                            })}
                                            key={i}
                                        >
                                            <TableCell>{capFChar(l.from)}</TableCell>
                                            <TableCell align="right">
                                                {capFChar(l.to)}
                                            </TableCell>
                                            <TableCell align="right">
                                                {l.createdOn.toLocaleString("tr-TR")}
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                ) : (
                    <Typography color="textPrimary" variant="h4">
                        Send your love by clicking the heart!
                    </Typography>
                )}

                <Grid item xs={12}>
                    <Line
                        data={chartData}
                    />
                </Grid>

            </Grid>
        </Box>
    );
}
