import firebase from "./firebase";
import "firebase/firestore";
import { serverizeStr } from "./utils";

const db = firebase.firestore();
const recipients = db.collection("@recipients");
const loves = db.collection("@loves");

const getServerTime = () => firebase.firestore.FieldValue.serverTimestamp();

type DocSnap = firebase.firestore.QueryDocumentSnapshot<firebase.firestore.DocumentData>;
function sortDate(a: DocSnap, b: DocSnap): any {
    const aCreated = a.data()["createdOn"];
    const bCreated = b.data()["createdOn"];

    const A = aCreated ? aCreated.toDate().getTime() : new Date().getTime();
    const B = bCreated ? bCreated.toDate().getTime() : new Date().getTime();
    return A - B;
}

export async function addRecipients(r1: string, r2: string) {
    const safe_r1 = serverizeStr(r1);
    const safe_r2 = serverizeStr(r2);

    const r1Exists = (await recipients.doc(safe_r1).get()).exists;
    if (!r1Exists) {
        await recipients.doc(safe_r1).set({
            name: safe_r1,
            createdBy: safe_r1,
            createdOn: getServerTime(),
        });
    }

    const r2Exists = (await recipients.doc(safe_r2).get()).exists;
    if (!r2Exists) {
        await recipients.doc(safe_r2).set({
            name: safe_r2,
            createdBy: safe_r1,
            createdOn: getServerTime(),
        });
    }
}

export function getLovesFor(recipient: string, then: (res: any[]) => void) {
    const safe_r = serverizeStr(recipient);

    loves.where("to", "==", safe_r).onSnapshot((qs1) => {
        loves.where("from", "==", safe_r).onSnapshot((qs2) => {
            const res = [...qs1.docs, ...qs2.docs].sort(sortDate).map((doc) => {
                const from = doc.data()["from"];
                const to = doc.data()["to"];
                const loveTime = doc.data()["createdOn"];

                return {
                    from,
                    to,
                    createdOn: loveTime ? loveTime.toDate() : new Date(),
                };
            });

            then(res.reverse());
        });
    });
}

export async function addLove(from: string, to: string) {
    await loves.add({
        to: serverizeStr(to),
        from: serverizeStr(from),
        createdOn: getServerTime(),
    });
}

export function getLovesBetween(
    r1: string,
    r2: string,
    then: (res: any[]) => void
) {
    const safe_r1 = serverizeStr(r1);
    const safe_r2 = serverizeStr(r2);

    loves
        .where("from", "in", [safe_r1, safe_r2])
        .onSnapshot((querySnapshot1) => {
            loves
                .where("to", "in", [safe_r1, safe_r2])
                .onSnapshot((querySnapshot2) => {
                    const res1 = querySnapshot1.docs;
                    const res2 = querySnapshot2.docs;
                    const res = res1
                        .concat(res2)
                        .sort(sortDate)
                        .map((l) => {
                            const from = l.data()["from"];
                            const to = l.data()["to"];
                            const loveTime = l.data()["createdOn"];

                            return {
                                from,
                                to,
                                createdOn: loveTime
                                    ? loveTime.toDate()
                                    : new Date(),
                            };
                        });

                    then(res.reverse());
                });
        });
}
