export const capFChar = (s: string) => {
    if (!s) return "";

    const words = s.trim().replace(/\s+/g, " ").split(" ");
    const capped = words.map((w) => {
        const w_t = w.trim();
        return w_t[0].toUpperCase() + w_t.slice(1);
    });

    return capped.join(" ");
};

export function groupByHour(loves: any[]) {
    return loves.reduce((acc, l) => {
        const from = serverizeStr(l.from);
        if (!acc[from]) acc[from] = Array(24).fill(0);
        acc[from][l.createdOn.getHours()]++;
        return acc;
    }, {});
}

export const serverizeStr = (s: string) =>
    s.toLowerCase().trim().replace(/\s+/g, " ");
