import React from "react";
import {
    Box,
    Button,
    CircularProgress,
    Grid,
    makeStyles,
    Typography,
    withStyles,
} from "@material-ui/core";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import FavoriteIcon from "@material-ui/icons/Favorite";
import MUITextField from "@material-ui/core/TextField";
import { pulse } from "react-animations";
import clsx from "clsx";
import { addRecipients } from "./dbUtils";

const useStyle = makeStyles((theme) => ({
    center: {
        textAlign: "center",
    },
    inputContainer: {
        alignItems: "center",
        display: "inline-flex",
        marginTop: theme.spacing(10),
    },
    button: {},
    "@keyframes pulse": pulse,
    pulse: {
        animation: `$pulse 1000ms`,
    },
}));
const TextField = withStyles((theme) => ({
    root: {
        "& .MuiOutlinedInput-root": {
            "& fieldset": {
                borderColor: theme.palette.secondary.main,
            },
            "&:hover fieldset": {
                borderColor: theme.palette.primary.main,
            },
        },
    },
}))(MUITextField);

export default function SetupPage({ onDone, initValue }) {
    const s = useStyle();
    const [from, setFrom] = React.useState(initValue ? initValue.from : "");
    const [to, setTo] = React.useState(initValue ? initValue.to : "");
    const [loading, setLoading] = React.useState(false);

    const handleStart = async () => {
        if (from !== "" && to !== "") {
            setLoading(true);
            await addRecipients(from, to);
            onDone(from, to);
            setLoading(false);
        }
    };

    const handleKeyDown = (event) => {
        if (event.keyCode === 13) {
            handleStart();
        }
    };

    return (
        <Box className={s.center}>
            <Grid container spacing={1}>
                <Grid item xs={12} sm={2}>
                    <Typography color="textPrimary" variant="h3">
                        ðŸ’™
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={8}>
                    <Typography color="textPrimary" variant="h3">
                        Love Counter
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={2}>
                    <Typography color="textPrimary" variant="h3">
                        ðŸ’™
                    </Typography>
                </Grid>
            </Grid>

            <Typography color="textPrimary" gutterBottom variant="h5">
                Enter your names below to start sending love.
            </Typography>
            <Grid container spacing={3} className={s.inputContainer}>
                <Grid item xs={12} sm={5} md={4}>
                    <TextField
                        fullWidth
                        label="Your Name"
                        variant="outlined"
                        value={from}
                        onKeyDown={handleKeyDown}
                        onChange={(e) => setFrom(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={2} md={1}>
                    <ArrowForwardIcon color="primary" fontSize="large" />
                </Grid>
                <Grid item xs={12} sm={5} md={3}>
                    <TextField
                        fullWidth
                        label="Their Name"
                        variant="outlined"
                        value={to}
                        onKeyDown={handleKeyDown}
                        onChange={(e) => setTo(e.target.value)}
                    />
                </Grid>
                <Grid item xs={12} sm={12} md={4}>
                    {loading ? (
                        <CircularProgress color="primary" />
                    ) : (
                        <Button
                            fullWidth
                            size="large"
                            onClick={handleStart}
                            className={clsx(s.button, {
                                [s.pulse]: from !== "" && to !== "",
                            })}
                            disabled={from === "" || to === ""}
                            color="primary"
                            variant="contained"
                            startIcon={<FavoriteIcon />}
                            endIcon={<FavoriteIcon />}
                        >
                            Start
                        </Button>
                    )}
                </Grid>
            </Grid>
        </Box>
    );
}
