import React from "react";
import { IconButton, makeStyles } from "@material-ui/core";
import FavoriteBorderIcon from "@material-ui/icons/FavoriteBorder";
import FavoriteIcon from "@material-ui/icons/Favorite";
import clsx from "clsx";
import { tada, bounceIn } from "react-animations";

const useStyle = makeStyles((theme) => ({
    button: {},
    heartIcon: {},
    "@keyframes tada": tada,
    pulse: {
        animation: `$tada 1000ms`,
    },
    "@keyframes bounceIn": bounceIn,
    pulseExit: {
        animation: `$bounceIn 1000ms`,
    },

    "@media (min-width: 0px)": {
        heartIcon: {
            fontSize: "10rem",
        },
    },
    "@media (min-width: 960px)": {
        heartIcon: {
            fontSize: "20rem",
        },
    },
}));

export default function LoveButton({ onClick }) {
    const s = useStyle();
    const [loved, setLoved] = React.useState(false);

    return (
        <IconButton
            onClick={() => {
                setLoved(true);
                setTimeout(() => setLoved(false), 2000);
                onClick();
            }}
            className={clsx(s.button, {
                [s.pulse]: loved,
                [s.pulseExit]: !loved,
            })}
            color="primary"
        >
            {loved ? (
                <FavoriteIcon className={s.heartIcon} />
            ) : (
                <FavoriteBorderIcon className={s.heartIcon} />
            )}
        </IconButton>
    );
}
